import { Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { Subscription } from 'rxjs';
import { ColumnDisplayType, FixedDataSource, ICombinedColumn, ISearchItems, TableItem } from 'shared-code-lib';
import {
  DocumentEntityType,
  Inbound,
  SharePermissions,
  ShareWith,
  ShareWithType,
} from '../../../../blocks/InterFAX/Models/inbound';
import { InterfaxDocumentSharingService } from '../../../../blocks/services/interfax-document-sharing.service';
import { FaxUserStatus, Group, IFaxUser } from './../../../../blocks/InterFAX/Models/admin';
import { InterFAXService } from './../../../../blocks/services/interfax.service';
import { SpinnerStateService } from './../../../../blocks/services/spinner-state.service';

class Item extends ShareWith {}

export enum autoShareDialogState {
  Edit = 'Edit',
  New = 'New',
}

@Component({
  selector: 'app-auto-share',
  templateUrl: './auto-share.component.html',
  styleUrls: ['./auto-share.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AutoShareComponent implements OnInit, OnDestroy, ISearchItems {
  @Input() inboundFaxSettings: Inbound;
  @Input() preferencesKey: string;
  @Input() selectedUser: string;

  autoShareItems: TableItem[] = [];
  autoShareColumns: ICombinedColumn[];
  autoShareSort: SortDescriptor;
  entityIcons: Array<string> = ['fa-user fa-lg', 'fa-users fa-lg', 'fa-globe fa-lg'];
  showShareModal = false;
  modalShare: ShareWith;
  shareKey: string = '';
  selectedUsers: Array<ShareWith>;
  permissionDataSource: FixedDataSource;
  errors = new Array<string>();
  dialogState = autoShareDialogState.New;
  usersNotLoaded = true;
  itemsLoaded = false;

  allUsers: Array<ShareWith> = [];

  subscriptions = new Subscription();
  faxUsersSubscription: Subscription;

  constructor(
    private translateService: TranslateService,
    private ngxSmartModalService: NgxSmartModalService,
    private spinnerService: SpinnerStateService,
    private interfaxService: InterFAXService,
    private interfaxDocumentSharingService: InterfaxDocumentSharingService
  ) {}

  ngOnInit() {
    this.loadGroups();
    this.autoShareColumns = this.getColumns();

    this.permissionDataSource = new FixedDataSource(this.interfaxDocumentSharingService.options);

    const defaultSort: SortDescriptor = {
      field: '1',
      dir: 'asc',
    };

    this.autoShareSort = defaultSort;

    this.subscriptions.add(
      this.ngxSmartModalService.getModal('shareModal').onClose.subscribe(() => this.onCloseShareModal())
    );
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  createTableItem(shareWith: ShareWith): TableItem {
    this.setColumnData(shareWith);
    const tableItem = new TableItem(shareWith, this.autoShareColumns, this.translateService);

    tableItem.data.defaultAction = {
      name: this.translateService.instant('admin-inbound-fax-settings.properties'),
    };

    return tableItem;
  }

  setColumnData(shareWith: ShareWith) {
    const item = this.allUsers.find((u) => u.equals(shareWith));
    shareWith['icon'] = item ? item['icon'] : this.entityIcons[shareWith.entityType - 1];

    this.setPermissionText(shareWith);
  }

  setPermissionText(shareWith: ShareWith) {
    let permissionText = '';

    const field = this.permissionDataSource.options.find((p) => p.value === `${shareWith.permissions}`);
    if (field) {
      permissionText = field.name;
    }

    shareWith['permissionsText'] = permissionText;
  }

  getColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(this.createColumn('type', ColumnDisplayType.FontAwesome, '', 32, 'icon'));

    columns.push(
      this.createColumn(
        'destination',
        ColumnDisplayType.ActionLink,
        this.translateService.instant('admin-inbound-fax-settings.column-destination'),
        200,
        'name'
      )
    );

    columns.push(
      this.createColumn(
        'permissions',
        ColumnDisplayType.Text,
        this.translateService.instant('admin-inbound-fax-settings.column-permissions'),
        200,
        'permissionsText',
        true
      )
    );

    return columns;
  }

  createColumn(
    id: string,
    displayType: number,
    columnName: string,
    width: number,
    itemProperty: string,
    stretch = false
  ) {
    return {
      id: 'column_' + id,
      width: width,
      stretch: stretch,
      displayType: displayType,
      headerImage: '',
      headerText: columnName,
      itemProperty: itemProperty,
      columnName: columnName,
      sortable: false,
      sortByFieldName: '',
      invertBooleanValue: false,
    };
  }

  onEnabledChecked(event: any) {
    this.inboundFaxSettings.autoShare.enabled = !this.inboundFaxSettings.autoShare.enabled;
  }

  refreshShareTableItems() {
    this.inboundFaxSettings.autoShare.shares = this.inboundFaxSettings.autoShare.shares.slice();
  }

  openShareModal = () => this.ngxSmartModalService.getModal('shareModal').open();

  onAddShare = (): void => {
    this.dialogState = autoShareDialogState.New;
    this.errors = [];
    this.shareKey = '';
    this.modalShare = new Item({
      shareWithType: ShareWithType.User,
      permissions: SharePermissions.View,
      name: '',
      entity: '',
      entityType: DocumentEntityType.Account,
    });
    this.selectedUsers = new Array<ShareWith>();
    this.showShareModal = true;
    this.ngxSmartModalService.getModal('shareModal').open();
  };

  onDeleteShare = (item: Item): void => {
    this.inboundFaxSettings.autoShare.shares = this.inboundFaxSettings.autoShare.shares.filter(
      (s) => s.entity != item.entity
    );

    this.autoShareItems = this.inboundFaxSettings.autoShare.shares.map((e) => this.createTableItem(e));
  };

  onPropertiesShare = (item: Item): void => {
    this.dialogState = autoShareDialogState.Edit;
    this.errors = [];
    this.modalShare = new Item(item);
    this.shareKey = item.entity;
    this.selectedUsers = new Array<ShareWith>();
    this.showShareModal = true;
    this.ngxSmartModalService.getModal('shareModal').open();
  };

  onSaveShareModal = (): void => {
    //todo: validate unique and not blank
    this.errors = [];
    if (!this.isShareModalValid()) {
      this.errors.push(this.translateService.instant('admin-inbound-fax-settings.share-modal-not-valid'));
      return;
    }

    if (this.shareKey) {
      let shareIndex = this.inboundFaxSettings.autoShare.shares.findIndex((s) => s.entity === this.shareKey);
      if (shareIndex >= 0) {
        if (this.modalShare.entityType == DocumentEntityType.Account)
          this.modalShare.permissions = SharePermissions.View;

        this.inboundFaxSettings.autoShare.shares[shareIndex] = this.modalShare;
      }
    } else {
      this.selectedUsers.forEach((s) => {
        let share = new ShareWith(s);
        share.permissions =
          s.entityType == DocumentEntityType.Account ? SharePermissions.View : this.modalShare.permissions;

        this.inboundFaxSettings.autoShare.shares = this.inboundFaxSettings.autoShare.shares.filter(
          (s) => s.entity !== share.entity
        );

        this.inboundFaxSettings.autoShare.shares.push(share);
      });
    }

    this.autoShareItems = this.inboundFaxSettings.autoShare.shares.map((e) => this.createTableItem(e));

    this.onCloseShareModal();
  };

  onCloseShareModal = (): void => {
    this.spinnerService.hide();
    this.ngxSmartModalService.getModal('shareModal').close();
    this.showShareModal = false;
    this.usersNotLoaded = true;
    this.faxUsersSubscription.unsubscribe();
  };

  isShareModalValid(): boolean {
    return (this.selectedUsers && this.selectedUsers.length > 0) || this.dialogState == autoShareDialogState.Edit;
  }

  private loadGroups = () => {
    // show spinner on this.spinnerWrapper parent
    const spinnerElement = document.getElementById('spinnerPanel');
    this.spinnerService.show(this.translateService.instant('Common.LoadingData'), spinnerElement);
    this.subscriptions.add(this.interfaxService.getGroups(this.groupsLoaded, this.groupsLoadFailed));
  };

  groupsLoaded = (groups: Array<Group>) => {
    this.allUsers = groups.map((g) => {
      let shareUser = new ShareWith(this.modalShare);
      shareUser.entity = g.id;
      shareUser.name = g.name;
      shareUser.entityType = DocumentEntityType.Group;
      shareUser['icon'] = 'fa-users';
      return shareUser;
    });

    this.addEveryone();

    this.inboundFaxSettings.autoShare.shares.forEach((share) => {
      this.autoShareItems.push(this.createTableItem(share));
    });

    this.itemsLoaded = true;
    this.spinnerService.hide();
  };

  groupsLoadFailed = (err: any) => {
    this.spinnerService.hide();
  };

  addEveryone = () => {
    const everyone = new ShareWith(this.modalShare);
    everyone.entity = '';
    everyone.name = this.translateService.instant('admin-inbound-fax-settings.everyone');
    everyone.entityType = DocumentEntityType.Account;
    everyone['icon'] = 'fa-globe';
    this.allUsers.push(everyone);
  };

  findMatches(searchTerm: string, completed: (items: Array<any>) => void): void {
    this.faxUsersSubscription = this.interfaxService.getFaxUsers(
      (items: Array<IFaxUser>) => this.findMatchesSucceeded(items, completed),
      () => {},
      searchTerm,
      FaxUserStatus.Open,
      [this.selectedUser.trim()]
    );

    this.subscriptions.add(this.faxUsersSubscription);
  }

  findMatchesSucceeded = (users: Array<IFaxUser>, completed: (items: Array<any>) => void): void => {
    const userShareWith = users.map((u) => {
      let shareUser = new ShareWith(this.modalShare);
      shareUser.entity = u.userId;
      shareUser.name = u.userId;
      shareUser.entityType = DocumentEntityType.User;
      shareUser['icon'] = u.iconUrl;
      return shareUser;
    });

    let allEntities = this.allUsers.concat(userShareWith);

    allEntities = allEntities.filter(
      (shareWith) =>
        !(shareWith.entityType === DocumentEntityType.User && shareWith.entity.trim() === this.selectedUser.trim())
    );

    completed(allEntities);
  };
}
