import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ContactList, FieldDefinition, MetaDataType, RolePermissions } from 'shared-code-lib';
import { Inbound } from '../../../../blocks/InterFAX/Models/inbound';
import { UserStateService } from '../../../../blocks/services/user-state.service';

@Component({
  selector: 'app-inbound-advanced-fax-settings',
  templateUrl: './inbound-advanced-fax-settings.component.html',
  styleUrls: ['./inbound-advanced-fax-settings.component.scss'],
})
export class InboundAdvancedFaxSettingsComponent implements OnInit {
  @Input() inboundFaxSettings: Inbound;
  @Input() contactLists: Array<ContactList>;
  @Input() selectedUser: string;

  timeoutPeriodInMinutes: FieldDefinition;
  faxNumber: FieldDefinition;

  allowAutoShare: boolean;
  allowAutoForward: boolean;

  get backupTimeoutPeriodInMinutes() {
    return `${this.inboundFaxSettings.backup.timeoutPeriodInMinutes}`;
  }
  set backupTimeoutPeriodInMinutes(val: string) {
    this.inboundFaxSettings.backup.timeoutPeriodInMinutes = Number(val);
  }

  constructor(private translateService: TranslateService, private userStateService: UserStateService) {}

  ngOnInit(): void {
    this.createFields();

    this.allowAutoShare = this.userStateService.hasRolePermission(RolePermissions.INTERFAX_PERMISSIONS_ALLOWAUTOSHARE);
    this.allowAutoForward =
      this.userStateService.hasRolePermission(RolePermissions.INTERFAX_PERMISSIONS_ALLOWFORWARDINGINBOUNDPCI) &&
      this.inboundFaxSettings.hasOutboundService;
  }

  createFields = (): void => {
    this.timeoutPeriodInMinutes = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-inbound-fax-settings.time-to-wait-before'),
      placeholder: this.translateService.instant('admin-inbound-fax-settings.time-to-wait-before-placeholder'),
      required: false,
      allowNegativeNumbers: false,
    });

    this.faxNumber = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-inbound-fax-settings.fax-number'),
      placeholder: this.translateService.instant('admin-inbound-fax-settings.fax-number-placeholder'),
      required: true,
    });
  };
}
