import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from '../../../../../../../../../Bamba.Shared';
import { EFAXConfiguration } from '../../../../../blocks/InterFAX/Models/webintegration';

@Component({
  selector: 'app-webpost-efax',
  templateUrl: './webpost-efax.component.html',
  styleUrls: ['./webpost-efax.component.scss'],
})
export class WebpostEfaxComponent implements OnInit {
  @Input() configuration: EFAXConfiguration;

  fileTypeField: FieldDefinition;
  contentTypeField: FieldDefinition;
  userNameField: FieldDefinition;
  passwordField: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields = (): void => {
    this.fileTypeField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-inbound-fax-settings.efax-fileType'),
      placeholder: '',
      required: true,
      datasource: this.fileTypeFormatDataSource(),
    });

    this.contentTypeField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-inbound-fax-settings.efax-contentType'),
      placeholder: '',
      required: true,
      datasource: this.contentTypeFormatDataSource(),
    });

    this.userNameField = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-inbound-fax-settings.efax-username'),
      placeholder: this.translateService.instant('admin-inbound-fax-settings.efax-username-placeholder'),
    });

    this.passwordField = new FieldDefinition({
      type: MetaDataType.Password,
      label: this.translateService.instant('admin-inbound-fax-settings.efax-password'),
      placeholder: this.translateService.instant('admin-inbound-fax-settings.efax-password-placeholder'),
    });
  };

  fileTypeFormatDataSource(): FixedDataSource {
    const options = new Array<IFieldOptions>();

    options.push({ name: this.translateService.instant('admin-inbound-fax-settings.efax-filetype-pdf'), value: 'pdf' });
    options.push({ name: this.translateService.instant('admin-inbound-fax-settings.efax-filetype-tif'), value: 'tif' });

    return new FixedDataSource(options);
  }

  contentTypeFormatDataSource(): FixedDataSource {
    const options = new Array<IFieldOptions>();

    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.efax-contenttype-entire'),
      value: 'Entire',
    });
    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.efax-contenttype-paged'),
      value: 'Paged',
    });

    return new FixedDataSource(options);
  }
}
