import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ContactList, FieldDefinition, FixedDataSource, MetaDataType, RolePermissions } from 'shared-code-lib';
import { RedistributeTo } from '../../../../../blocks/InterFAX/Models/inbound';
import { UserStateService } from '../../../../..//blocks/services/user-state.service';

@Component({
  selector: 'app-auto-forward-destination-modal',
  templateUrl: './auto-forward-destination-modal.component.html',
  styleUrls: ['./auto-forward-destination-modal.component.scss'],
})
export class AutoForwardDestinationModalComponent implements OnInit {
  @Input() redistributeTo: RedistributeTo;
  @Input() contactLists: Array<ContactList>;
  @Input() isPCIUser: boolean;

  fieldRedistributeType: FieldDefinition;

  fieldFaxNumber: FieldDefinition;
  fieldContactList: FieldDefinition;

  get redistributeToType() {
    return `${this.redistributeTo.redistributeToType}`;
  }
  set redistributeToType(val: string) {
    this.redistributeTo.redistributeToType = Number(val);
  }

  constructor(private translateService: TranslateService, private userStateService: UserStateService) {}

  ngOnInit(): void {
    this.contactLists.sort((a, b) => a.name.localeCompare(b.name));
    this.createFields();
  }

  createFields = (): void => {
    const redistributeOptions = [];
    redistributeOptions.push({ name: this.translateService.instant('RedistributeToTypeEnum.Fax'), value: '0' });

    if (
      !this.isPCIUser &&
      this.userStateService.hasRolePermission(RolePermissions.INTERFAX_PERMISSIONS_ALLOWCONTACTLISTS)
    ) {
      redistributeOptions.push({
        name: this.translateService.instant('RedistributeToTypeEnum.ContactList'),
        value: '1',
      });

      const contactListOptions = this.contactLists.map((cl) => {
        return { name: cl.name, value: cl.id.toString() };
      });

      this.fieldContactList = new FieldDefinition({
        type: MetaDataType.List,
        label: this.translateService.instant('admin-inbound-fax-settings.auto-forward-contactlist'),
        required: false,
        datasource: new FixedDataSource(contactListOptions),
      });
    }

    this.fieldRedistributeType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-inbound-fax-settings.auto-forward-type'),
      required: false,
      datasource: new FixedDataSource(redistributeOptions),
    });

    this.fieldFaxNumber = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-inbound-fax-settings.auto-forward-destination'),
      placeholder: this.translateService.instant('admin-inbound-fax-settings.auto-forward-destination-placeholder'),
      required: true,
    });
  };

  onTypeChanged($event: string) {
    this.redistributeTo.to = null;
  }
}
