import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import {
  ColumnDisplayType,
  EnumDataSource,
  Feature,
  FieldDefinition,
  FixedDataSource,
  ICombinedColumn,
  IFieldOptions,
  MetaDataType,
  RolePermissions,
  TableItem,
} from 'shared-code-lib';
import { Email } from '../../../../blocks/core/email';
import { DeleteFaxImageOptions, EmailFormat, Inbound, Notification } from '../../../../blocks/InterFAX/Models/inbound';
import {
  EFAXConfiguration,
  HttpConfiguration,
  WebIntegrationType,
} from '../../../../blocks/InterFAX/Models/webintegration';
import { UserStateService } from '../../../../blocks/services/user-state.service';

class Item extends Notification {}

@Component({
  selector: 'app-inbound-general-fax-settings',
  templateUrl: './inbound-general-fax-settings.component.html',
  styleUrls: ['./inbound-general-fax-settings.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class InboundGeneralFaxSettingsComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() inboundFaxSettings: Inbound;
  notificationItems: TableItem[] = [];
  notificationColumns: ICombinedColumn[];
  notificationSort: SortDescriptor;
  emailFormatText: Array<string> = [];
  attachmentFormat: FieldDefinition;
  deleteFaxImage: FieldDefinition;
  webServiceIntegrationType: FieldDefinition;
  webServiceIntegrationUrl: FieldDefinition;
  showNotificationEmailModal = false;
  modalEditNotification: Notification;
  notificationEmailKey: string = '';
  advancedRoutingEnabled: boolean;

  substitutionVariables: FixedDataSource;

  notificationEmailModalInstance: NgxSmartModalComponent;

  efaxConfiguration = new EFAXConfiguration({ fileType: 'pdf', contentType: 'Entire', userName: '', password: '' });
  httpConfiguration = new HttpConfiguration({});

  allowEmailNotifications: boolean;
  isPCI: boolean;

  subscriptions = new Subscription();

  get inboundWebIntegrationType() {
    if (this.inboundFaxSettings.webIntegration.webIntegrationType == WebIntegrationType.HTTPPost) return 'Post';
    if (this.inboundFaxSettings.webIntegration.webIntegrationType == WebIntegrationType.XMLWebService) return 'WS';
    if (this.inboundFaxSettings.webIntegration.webIntegrationType == WebIntegrationType.Extended)
      return this.inboundFaxSettings.webIntegration.extendedConfiguration.type;

    return ''; // rought-roo
  }

  set inboundWebIntegrationType(val: string) {
    if (val == 'WS') this.inboundFaxSettings.webIntegration.webIntegrationType = WebIntegrationType.XMLWebService;
    if (val == 'Post') this.inboundFaxSettings.webIntegration.webIntegrationType = WebIntegrationType.HTTPPost;

    if (val == 'Http') {
      this.inboundFaxSettings.webIntegration.webIntegrationType = WebIntegrationType.Extended;
      this.inboundFaxSettings.webIntegration.extendedConfiguration = this.httpConfiguration;
    }

    if (val == 'EFax') {
      this.inboundFaxSettings.webIntegration.webIntegrationType = WebIntegrationType.Extended;
      this.inboundFaxSettings.webIntegration.extendedConfiguration = this.efaxConfiguration;
    }
  }

  get deleteFaxImageOption() {
    return `${this.inboundFaxSettings.deleteFaxImage}`;
  }
  set deleteFaxImageOption(val: string) {
    this.inboundFaxSettings.deleteFaxImage = Number(val);
  }

  constructor(
    private translate: TranslateService,
    private ngxSmartModalService: NgxSmartModalService,
    private userStateService: UserStateService,
    private toastrService: ToastrService
  ) {}

  ngOnInit(): void {
    this.createSubstitutionVariables();
    this.createFields();
    this.createEmailFormatTextOptions();
    this.notificationColumns = this.getColumns();

    const defaultSort: SortDescriptor = {
      field: '1',
      dir: 'asc',
    };

    this.notificationSort = defaultSort;
    this.inboundFaxSettings.notifications.forEach((notification) => {
      this.notificationItems.push(this.createTableItem(notification));
    });

    this.inboundFaxSettings.webIntegration.enabled;

    this.allowEmailNotifications = this.userStateService.hasRolePermission(
      RolePermissions.INTERFAX_PERMISSIONS_ALLOWINBOUNDEMAILNOTIFICATIONS
    );

    this.setExtendedConfiguration();

    this.advancedRoutingEnabled = this.userStateService.hasFeature(Feature.InterFAXAdvanced);
  }

  ngAfterViewInit(): void {
    this.notificationEmailModalInstance = this.ngxSmartModalService.getModal('notificationEmailModal');
    if (!this.notificationEmailModalInstance) throw new Error('Failed to initialize: notificationEmailModalInstance');

    this.subscriptions.add(
      this.notificationEmailModalInstance.onClose.subscribe(() => (this.showNotificationEmailModal = false))
    );
  }

  createEmailFormatTextOptions = (): void => {
    this.emailFormatText.push(this.translate.instant('EmailFormatEnum.PlainWithLink'));
    this.emailFormatText.push(this.translate.instant('EmailFormatEnum.Plain'));
    this.emailFormatText.push(this.translate.instant('EmailFormatEnum.Html'));
    this.emailFormatText.push(this.translate.instant('EmailFormatEnum.NEC'));
  };

  onWebIntegrationChanged = (value: string): void => {
    this.webServiceIntegrationUrl.substitutable = this.inboundWebIntegrationType == 'Http';
  };

  setExtendedConfiguration = (): void => {
    if (this.inboundWebIntegrationType == 'Http') {
      this.httpConfiguration = <HttpConfiguration>this.inboundFaxSettings.webIntegration.extendedConfiguration;
    }

    if (this.inboundWebIntegrationType == 'EFax') {
      this.efaxConfiguration = <EFAXConfiguration>this.inboundFaxSettings.webIntegration.extendedConfiguration;
    }
  };

  attachmentFormatDataSource(): FixedDataSource {
    const attachmentFormatOptions = new Array<IFieldOptions>();
    attachmentFormatOptions.push({
      name: this.translate.instant('admin-inbound-fax-settings.attachment-format-pdf'),
      value: this.translate.instant('admin-inbound-fax-settings.attachment-format-pdf'),
    });
    attachmentFormatOptions.push({
      name: this.translate.instant('admin-inbound-fax-settings.attachment-format-tif'),
      value: this.translate.instant('admin-inbound-fax-settings.attachment-format-tif'),
    });
    attachmentFormatOptions.push({
      name: this.translate.instant('admin-inbound-fax-settings.attachment-format-tiff-g3'),
      value: this.translate.instant('admin-inbound-fax-settings.attachment-format-tiff-g3'),
    });
    attachmentFormatOptions.push({
      name: this.translate.instant('admin-inbound-fax-settings.attachment-format-tiff-g4'),
      value: this.translate.instant('admin-inbound-fax-settings.attachment-format-tiff-g4'),
    });

    return new FixedDataSource(attachmentFormatOptions);
  }

  createFields = (): void => {
    this.attachmentFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-inbound-fax-settings.attachment-format'),
      placeholder: '',
      required: true,
      datasource: this.attachmentFormatDataSource(),
    });

    this.deleteFaxImage = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-inbound-fax-settings.delete-fax-image-after-notification'),
      required: true,
      datasource: new EnumDataSource(DeleteFaxImageOptions, 'DeleteFaxImageOptionsEnum', this.translate),
    });

    this.webServiceIntegrationType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-inbound-fax-settings.web-service-integration'),
      required: true,
      datasource: this.webIntegrationTypesDataSource(),
    });

    this.webServiceIntegrationUrl = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-inbound-fax-settings.url'),
      placeholder: this.translate.instant('admin-inbound-fax-settings.url-placeholder'),
      required: true,
      substitutable: this.inboundWebIntegrationType == 'Http',
      datasource: this.substitutionVariables,
    });
  };

  webIntegrationTypesDataSource(): FixedDataSource {
    const options = new Array<IFieldOptions>();

    options.push({ name: this.translate.instant('WebIntegrationTypeEnum.HTTPPost'), value: 'Post' });
    options.push({ name: this.translate.instant('WebIntegrationTypeEnum.XMLWebService'), value: 'WS' });
    options.push({ name: this.translate.instant('WebIntegrationTypeEnum.EFax'), value: 'EFax' });
    options.push({ name: this.translate.instant('WebIntegrationTypeEnum.Http'), value: 'Http' });

    return new FixedDataSource(options);
  }

  createSubstitutionVariables = (): void => {
    const options = new Array<IFieldOptions>();

    options.push({ name: 'TransactionType', value: 'TransactionType' });
    options.push({ name: 'TransactionID', value: 'TransactionID' });
    options.push({ name: 'PhoneNumber', value: 'PhoneNumber' });
    options.push({ name: 'MessageType', value: 'MessageType' });
    options.push({ name: 'MessageSize', value: 'MessageSize' });
    options.push({ name: 'RemoteCSID', value: 'RemoteCSID' });
    options.push({ name: 'PageCount', value: 'PageCount' });
    options.push({ name: 'Status', value: 'Status' });
    options.push({ name: 'RecordingDuration', value: 'RecordingDuration' });
    options.push({ name: 'ReceiveTimeUTC', value: 'ReceiveTimeUTC' });
    options.push({ name: 'CallerID', value: 'CallerID' });
    options.push({ name: 'DocumentAsPDFBase64', value: 'DocumentAsPDFBase64' });
    options.push({ name: 'DocumentAsTIFBase64', value: 'DocumentAsTIFBase64' });
    options.push({ name: 'DocumentAsPDFByteCount', value: 'DocumentAsPDFByteCount' });
    options.push({ name: 'DocumentAsTIFByteCount', value: 'DocumentAsTIFByteCount' });

    options.sort((x, y) => x.name.localeCompare(y.name));

    this.substitutionVariables = new FixedDataSource(options);
  };

  createTableItem(notification: Notification): TableItem {
    notification['icon'] = 'fa-envelope fa-lg';
    notification['emailFormatText'] = this.emailFormatText[notification.format];
    notification['optionsText'] = notification.sign ? this.translate.instant('admin-inbound-fax-settings.signed') : '';

    if (notification.encrypt) {
      if (notification.encrypt && notification['optionsText']) {
        notification['optionsText'] += this.translate.instant('admin-inbound-fax-settings.and');
      }
      notification['optionsText'] += this.translate.instant('admin-inbound-fax-settings.encrypted');
    }

    const hasDigitalIdResId = notification.hasDigitalId
      ? 'admin-inbound-fax-settings.digital-id-uploaded'
      : 'admin-inbound-fax-settings.digital-id-not-uploaded';
    notification['hasDigitalIdText'] = this.translate.instant(hasDigitalIdResId);

    const tableItem = new TableItem(notification, this.notificationColumns, this.translate);

    tableItem.data.defaultAction = {
      name: this.translate.instant('admin-inbound-fax-settings.properties'),
    };

    return tableItem;
  }

  getColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(this.createColumn('type', ColumnDisplayType.FontAwesome, '', 32, 'icon'));

    columns.push(
      this.createColumn(
        'destination',
        ColumnDisplayType.ActionLink,
        this.translate.instant('admin-inbound-fax-settings.notification-email'),
        200,
        'email',
        true
      )
    );
    columns.push(
      this.createColumn(
        'format',
        ColumnDisplayType.Text,
        this.translate.instant('admin-inbound-fax-settings.notification-email-format'),
        200,
        'emailFormatText'
      )
    );
    columns.push(
      this.createColumn(
        'options',
        ColumnDisplayType.Text,
        this.translate.instant('admin-inbound-fax-settings.column-options'),
        200,
        'optionsText'
      )
    );

    columns.push(
      this.createColumn(
        'digitalid',
        ColumnDisplayType.Text,
        this.translate.instant('admin-inbound-fax-settings.notification-digital-id-status'),
        200,
        'hasDigitalIdText'
      )
    );

    return columns;
  }

  createColumn(
    id: string,
    displayType: number,
    columnName: string,
    width: number,
    itemProperty: string,
    stretch = false
  ): ICombinedColumn {
    return {
      id: 'column_' + id,
      width: width,
      stretch: stretch,
      displayType: displayType,
      headerImage: '',
      headerText: columnName,
      itemProperty: itemProperty,
      columnName: columnName,
      sortable: false,
      sortByFieldName: '',
      invertBooleanValue: false,
    };
  }

  onAddNotificationEmail = (): void => {
    this.showNotificationEmailModal = true;
    this.notificationEmailKey = '';
    this.modalEditNotification = new Item({
      email: '',
      encrypt: false,
      sign: false,
      format: EmailFormat.Html,
      hasDigitalId: false,
    });
    this.notificationEmailModalInstance.open();
  };

  onDeleteNotificationEmail = (item: Item): void => {
    this.inboundFaxSettings.notifications = this.inboundFaxSettings.notifications.filter((n) => n.email != item.email);
    this.notificationItems = this.inboundFaxSettings.notifications.map((e) => this.createTableItem(e));
  };

  onPropertiesNotificationEmail = (item: Item): void => {
    this.modalEditNotification = new Item(item);
    this.notificationEmailKey = item.email;
    this.showNotificationEmailModal = true;
    this.notificationEmailModalInstance.open();
  };

  onSaveNotificationEmailModal = (): void => {
    if (
      this.inboundFaxSettings.notifications.some(
        (x) =>
          x.email.toLowerCase() === this.modalEditNotification.email.toLowerCase() &&
          this.modalEditNotification.email.toLowerCase() !== this.notificationEmailKey.toLowerCase()
      )
    ) {
      this.toastrService.error(this.translate.instant('admin-outbound-fax-settings.email-exists'));
      return;
    }

    if (this.notificationEmailKey) {
      let notificationIndex = this.inboundFaxSettings.notifications.findIndex(
        (n) => n.email === this.notificationEmailKey
      );
      if (notificationIndex >= 0) {
        this.inboundFaxSettings.notifications[notificationIndex] = this.modalEditNotification;
      }
    } else {
      this.inboundFaxSettings.notifications.push(this.modalEditNotification);
    }

    this.notificationItems = this.inboundFaxSettings.notifications.map((e) => this.createTableItem(e));
    this.onCloseNotificationEmailModal();
  };

  onCloseNotificationEmailModal = (): void => {
    this.notificationEmailModalInstance.close();
    this.showNotificationEmailModal = false;
  };

  notificationEmailIsValid = (): boolean => {
    return Email.isValid(this.modalEditNotification?.email);
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
