import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminInboundFaxSettingsComponent } from './admin-inbound-fax-settings.component';

const routes: Routes = [
  {
    path: '',
    data: { type: 'properties' },
    component: AdminInboundFaxSettingsComponent,
    pathMatch: 'full',
  },
  {
    path: ':username',
    data: { type: 'properties' },
    component: AdminInboundFaxSettingsComponent,
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminInboundFaxSettingsRoutingModule {}
