import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, FieldDefinition, MetaDataType } from 'shared-code-lib';
import { RestrictBy, RestrictByType } from '../../../../../blocks/InterFAX/Models/inbound';

@Component({
  selector: 'app-auto-forward-restriction-modal',
  templateUrl: './auto-forward-restriction-modal.component.html',
  styleUrls: ['./auto-forward-restriction-modal.component.scss'],
})
export class AutoForwardRestrictionModalComponent implements OnInit {
  @Input() restrictBy: RestrictBy;
  fieldRestrictionType: FieldDefinition;
  fieldDestination: FieldDefinition;

  get restrictByType() {
    return `${this.restrictBy.restrictByType}`;
  }
  set restrictByType(val: string) {
    this.restrictBy.restrictByType = Number(val);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields = (): void => {
    this.fieldRestrictionType = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-inbound-fax-settings.auto-forward-type'),
      required: false,
      datasource: new EnumDataSource(RestrictByType, 'RestrictByTypeEnum', this.translateService),
    });

    this.fieldDestination = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-inbound-fax-settings.auto-forward-id'),
      placeholder: this.translateService.instant('admin-inbound-fax-settings.auto-forward-id-placeholder'),
      required: true,
    });
  };

  onTypeChange($event) {
    this.restrictBy.by = null;
  }
}
