import { Location } from '@angular/common';
import { Component, ElementRef, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AlertsService, ContactList, Icons, RolePermissions, Tab } from 'shared-code-lib';
import { Inbound } from '../../../blocks/InterFAX/Models/inbound';
import { ErrorsService } from '../../../blocks/services/errors.service';
import { InterFAXService } from '../../../blocks/services/interfax.service';
import { ApiResults, Control } from '../../../blocks/services/models';
import { SpinnerStateService } from '../../../blocks/services/spinner-state.service';
import { UserStateService } from '../../../blocks/services/user-state.service';

@Component({
  selector: 'app-admin-inbound-fax-settings',
  templateUrl: './admin-inbound-fax-settings.component.html',
  styleUrls: ['./admin-inbound-fax-settings.component.scss'],
})
export class AdminInboundFaxSettingsComponent implements OnInit, OnDestroy {
  tabsContentScroll: PerfectScrollbarDirective;
  @ViewChild(PerfectScrollbarDirective, { static: false }) set content(content: PerfectScrollbarDirective) {
    if (content) {
      this.tabsContentScroll = content;
    }
  }
  @ViewChild('spinnerWrapper', { static: true }) spinnerWrapper: ElementRef;
  activeTab = 'inbound';
  tabs = new Array<Tab>();
  rightControls: Array<Control> = [];
  saveButton: Control;
  cancelButton: Control;
  settings: Inbound;

  routeParams: Params;

  contactLists: Array<ContactList> = [];

  selectedUser: string;

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private interfaxService: InterFAXService,
    private spinnerService: SpinnerStateService,
    private toastrService: ToastrService,
    private errorsService: ErrorsService,
    private route: ActivatedRoute,
    private location: Location,
    private userStateService: UserStateService,
    private alertsService: AlertsService
  ) {}

  ngOnInit(): void {
    this.createButtons();
    this.createTabs();

    this.route.params.subscribe((p: Params) => {
      this.routeParams = p;
      this.selectedUser = this.routeParams.username ?? this.userStateService.interFAXUsername;

      this.spinnerService.show(this.translateService.instant('Common.LoadingData'), document.documentElement);

      this.loadFaxSettings();
    });
  }

  private createButtons() {
    this.saveButton = new Control(
      this.onSave,
      this.translateService.instant('admin-tenant.save'),
      'btn btn-primary btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.cancelButton = new Control(
      this.onCancel,
      this.translateService.instant('admin-tenant.cancel'),
      'btn btn-light btn-minwidth',
      '',
      '',
      'btn',
      false
    );

    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);
  }

  private createTabs = () => {
    this.tabs.push(
      new Tab('inboundgeneral', this.translateService.instant('admin-inbound-fax-settings.inbound-general'))
    );
    this.tabs.push(new Tab('barcodes', this.translateService.instant('admin-inbound-fax-settings.tab-barcodes')));
    this.tabs.push(new Tab('advanced', this.translateService.instant('admin-inbound-fax-settings.tab-advanced')));
  };

  activateTab = (view) => {
    this.activeTab = view;
    if (this.tabsContentScroll) {
      this.tabsContentScroll.scrollTo(0, 0);
    }
  };

  private loadContacts = () => {
    this.subscriptions.add(this.interfaxService.getContactLists(this.contactListsLoaded, this.contactListsFailed));
  };

  contactListsLoaded = (contactLists: Array<ContactList>): void => {
    this.contactLists = contactLists;
    this.spinnerService.hide();
  };

  contactListsFailed = (err: any): void => {
    console.log('AdminInboundFaxSettingsComponent.contactListsFailed', err);
    this.spinnerService.hide();
  };

  private loadFaxSettings = () => {
    this.interfaxService.getInboundConfiguration(this.selectedUser, this.inboundLoaded, this.inboundFailed);
  };

  inboundLoaded = (i: Inbound) => {
    this.settings = i;

    if (
      !this.settings.isPCI &&
      this.userStateService.hasRolePermission(RolePermissions.INTERFAX_PERMISSIONS_ALLOWCONTACTLISTS)
    ) {
      return this.loadContacts();
    }

    this.spinnerService.hide();
  };

  inboundFailed = (err: any) => {
    console.log('AdminInboundFaxSettingsComponent.inboundFailed', err);

    this.spinnerService.hide();
  };

  onSave = () => {
    this.interfaxService.updateInboundConfiguration(
      this.selectedUser,
      this.settings,
      this.onSaveSucceeded,
      this.onFailedToSave
    );
    this.spinnerService.showSavingSpinner();
  };

  onCancel = () => {
    this.returnToPreviousPage();
  };

  onSaveSucceeded = (results: ApiResults) => {
    this.spinnerService.hide();

    if (!results.success) {
      this.errorsService.showErrorsModal(results.errors, 'admin-inbound-fax-settings.save-fail');
      return;
    }

    if (this.routeParams.username) {
      this.toastrService.success(this.translateService.instant('admin-inbound-fax-settings.save-success'));
      this.returnToPreviousPage();
      return;
    }

    this.alertsService.success(
      this.translateService.instant('admin-inbound-fax-settings.settings-header-text'),
      this.translateService.instant('admin-inbound-fax-settings.save-success-modal'),
      false,
      {
        buttonText: this.translateService.instant('Common.Close'),
        customClass: 'success-modal large-font',
        showIcon: Icons.GreenTick,
      }
    );

    this.loadFaxSettings();
  };

  onFailedToSave = (): void => {
    this.spinnerService.hide();
    this.toastrService.error(this.translateService.instant('admin-inbound-fax-settings.save-fail'));
  };

  returnToPreviousPage = (): void => {
    this.location.back();
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
