import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { NgxSmartModalComponent, NgxSmartModalService } from 'ngx-smart-modal';
import { Subscription } from 'rxjs';
import { ColumnDisplayType, ContactList, ICombinedColumn, TableItem } from 'shared-code-lib';
import {
  Inbound,
  RedistributeTo,
  RedistributeToType,
  RestrictBy,
  RestrictByType,
} from '../../../../blocks/InterFAX/Models/inbound';

@Component({
  selector: 'app-auto-forward',
  templateUrl: './auto-forward.component.html',
  styleUrls: ['./auto-forward.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AutoForwardComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() inboundFaxSettings: Inbound;
  @Input() contactLists: Array<ContactList>;

  autoForwardItems: TableItem[] = [];
  autoForwardColumns: ICombinedColumn[];
  autoForwardSort: SortDescriptor;
  forwardTypeIcons: Array<string> = ['fa-fax fa-lg', 'fa-address-book fa-lg'];

  restrictItems: TableItem[] = [];
  restrictColumns: ICombinedColumn[];
  restrictSort: SortDescriptor;
  restrictTypeIcons: Array<string> = ['fa-phone fa-lg', 'fa-fax fa-lg'];

  autoForwardDestinationModalInstance: NgxSmartModalComponent;
  showAutoForwardDestinationModal = false;
  autoForwardDestinationKey = '';
  modalEditAutoForwardDestination: RedistributeTo;

  autoForwardRestrictionModalInstance: NgxSmartModalComponent;
  showAutoForwardRestrictionModal = false;
  autoForwardRestrictionKey = '';
  modalEditAutoForwardRestriction: RestrictBy;

  autoForwardRestrictionModalTitle: string;
  autoForwardDestinationModalTitle: string;

  errors = new Array<string>();
  subscriptions = new Subscription();

  constructor(
    private translate: TranslateService,
    private ngxSmartModalService: NgxSmartModalService
  ) {}

  ngOnInit(): void {
    this.autoForwardColumns = this.getForwardColumns();
    const defaultSort: SortDescriptor = {
      field: '1',
      dir: 'asc',
    };

    this.autoForwardSort = defaultSort;
    this.inboundFaxSettings.redistribute.redistributions.forEach((redistribute) => {
      this.autoForwardItems.push(this.createForwardTableItem(redistribute));
    });

    this.restrictColumns = this.getRestrictColumns();

    this.restrictSort = defaultSort;
    this.inboundFaxSettings.redistribute.restrictions.forEach((restrict) => {
      this.restrictItems.push(this.createRestrictTableItem(restrict));
    });
  }

  ngAfterViewInit(): void {
    this.autoForwardDestinationModalInstance = this.ngxSmartModalService.getModal('autoForwardDestinationModal');
    if (!this.autoForwardDestinationModalInstance) {
      throw new Error('Failed to initialize: autoForwardDestinationModalInstance');
    }

    this.autoForwardRestrictionModalInstance = this.ngxSmartModalService.getModal('autoForwardRestrictionModal');
    if (!this.autoForwardRestrictionModalInstance) {
      throw new Error('Failed to initialize: autoForwardRestrictionModalInstance');
    }

    this.subscriptions.add(
      this.autoForwardDestinationModalInstance.onClose.subscribe(() => (this.showAutoForwardDestinationModal = false))
    );

    this.subscriptions.add(
      this.autoForwardRestrictionModalInstance.onClose.subscribe(() => (this.showAutoForwardRestrictionModal = false))
    );
  }

  createForwardTableItem(redistribute: RedistributeTo): TableItem {
    redistribute['icon'] = this.forwardTypeIcons[redistribute.redistributeToType];
    redistribute['displayDestination'] = this.getDisplayDestination(redistribute);

    const tableItem = new TableItem(redistribute, this.autoForwardColumns, this.translate);

    tableItem.data.defaultAction = {
      name: this.translate.instant('admin-inbound-fax-settings.properties'),
    };

    return tableItem;
  }

  getDisplayDestination = (r: RedistributeTo): string => {
    if (r.redistributeToType == RedistributeToType.Fax) return r.to;

    const contactList = this.contactLists.find((cl) => cl.id == r.to);
    if (contactList) return contactList.name;

    return r.to;
  };

  createRestrictTableItem(restrict: RestrictBy): TableItem {
    restrict['icon'] = this.restrictTypeIcons[restrict.restrictByType];

    const tableItem = new TableItem(restrict, this.restrictColumns, this.translate);

    tableItem.data.defaultAction = {
      name: this.translate.instant('admin-inbound-fax-settings.properties'),
    };

    return tableItem;
  }

  getForwardColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(this.createColumn('type', ColumnDisplayType.FontAwesome, '', 32, 'icon'));

    columns.push(
      this.createColumn(
        'to',
        ColumnDisplayType.ActionLink,
        this.translate.instant('admin-inbound-fax-settings.column-destination'),
        200,
        'displayDestination',
        true
      )
    );

    return columns;
  }

  getRestrictColumns(): Array<ICombinedColumn> {
    const columns: Array<ICombinedColumn> = [];

    columns.push(this.createColumn('type', ColumnDisplayType.FontAwesome, '', 32, 'icon'));

    columns.push(
      this.createColumn(
        'destination',
        ColumnDisplayType.ActionLink,
        this.translate.instant('admin-inbound-fax-settings.column-destination'),
        200,
        'by',
        true
      )
    );

    return columns;
  }

  createColumn(
    id: string,
    displayType: number,
    columnName: string,
    width: number,
    itemProperty: string,
    stretch = false
  ): ICombinedColumn {
    return {
      id: 'column_' + id,
      width: width,
      stretch: stretch,
      displayType: displayType,
      headerImage: '',
      headerText: columnName,
      itemProperty: itemProperty,
      columnName: columnName,
      sortable: false,
      sortByFieldName: '',
      invertBooleanValue: false,
    };
  }

  onEnabledChecked(event: any): void {
    this.inboundFaxSettings.redistribute.enabled = !this.inboundFaxSettings.redistribute.enabled;
  }

  onAddAutoForwardDestinationModal = (): void => {
    this.errors = [];
    this.showAutoForwardDestinationModal = true;
    this.autoForwardDestinationKey = '';
    this.modalEditAutoForwardDestination = new RedistributeTo({ to: '', redistributeToType: RedistributeToType.Fax });
    this.autoForwardDestinationModalTitle = this.translate.instant(
      'admin-inbound-fax-settings.add-auto-forward-destination-modal-title'
    );
    this.autoForwardDestinationModalInstance.open();
  };

  onDeleteAutoForwardDestination = (item: any): void => {
    this.inboundFaxSettings.redistribute.redistributions = this.inboundFaxSettings.redistribute.redistributions.filter(
      (r) => r.to != item.to
    );
    this.autoForwardItems = this.inboundFaxSettings.redistribute.redistributions.map((e) =>
      this.createForwardTableItem(e)
    );
  };

  onPropertiesAutoForwardDestination = (item: any): void => {
    this.errors = [];
    this.modalEditAutoForwardDestination = new RedistributeTo(item);
    this.autoForwardDestinationKey = item.to;
    this.showAutoForwardDestinationModal = true;
    this.autoForwardDestinationModalTitle = this.translate.instant(
      'admin-inbound-fax-settings.auto-forward-destination-properties-modal-title'
    );
    this.autoForwardDestinationModalInstance.open();
  };

  onSaveAutoForwardDestinationModal = (): void => {
    if (!this.isAutoForwardModalValid()) return;

    if (this.autoForwardDestinationKey) {
      let index = this.inboundFaxSettings.redistribute.redistributions.findIndex(
        (r) => r.to === this.autoForwardDestinationKey
      );
      if (index >= 0) {
        this.inboundFaxSettings.redistribute.redistributions[index] = this.modalEditAutoForwardDestination;
      }
    } else {
      this.inboundFaxSettings.redistribute.redistributions.push(this.modalEditAutoForwardDestination);
    }

    this.autoForwardItems = this.inboundFaxSettings.redistribute.redistributions.map((e) =>
      this.createForwardTableItem(e)
    );
    this.onCloseAutoForwardDestinationModal();
  };

  onCloseAutoForwardDestinationModal = (): void => {
    this.autoForwardDestinationModalInstance.close();
    this.showAutoForwardDestinationModal = false;
  };

  onAddAutoForwardRestriction = (): void => {
    this.errors = [];
    this.showAutoForwardRestrictionModal = true;
    this.autoForwardRestrictionKey = '';
    this.modalEditAutoForwardRestriction = new RestrictBy({ by: '', restrictByType: RestrictByType.CallerId });
    this.autoForwardRestrictionModalTitle = this.translate.instant(
      'admin-inbound-fax-settings.add-auto-forward-restriction-modal-title'
    );
    this.autoForwardRestrictionModalInstance.open();
  };

  onDeleteAutoForwardRestriction = (item: any): void => {
    this.inboundFaxSettings.redistribute.restrictions = this.inboundFaxSettings.redistribute.restrictions.filter(
      (r) => r.by != item.by
    );
    this.restrictItems = this.inboundFaxSettings.redistribute.restrictions.map((e) => this.createRestrictTableItem(e));
  };

  onPropertiesAutoForwardRestriction = (item: any): void => {
    this.errors = [];
    this.modalEditAutoForwardRestriction = new RestrictBy(item);
    this.autoForwardRestrictionKey = item.by;
    this.showAutoForwardRestrictionModal = true;
    this.autoForwardRestrictionModalTitle = this.translate.instant(
      'admin-inbound-fax-settings.auto-forward-restriction-modal-title'
    );
    this.autoForwardRestrictionModalInstance.open();
  };

  onSaveAutoForwardRestrictionModal = (): void => {
    //todo: validate unique and not blank
    if (!this.isAutoForwardRestrictionModalValid()) return;

    if (this.autoForwardRestrictionKey) {
      let index = this.inboundFaxSettings.redistribute.restrictions.findIndex(
        (r) => r.by === this.autoForwardRestrictionKey
      );
      if (index >= 0) {
        this.inboundFaxSettings.redistribute.restrictions[index] = this.modalEditAutoForwardRestriction;
      }
    } else {
      this.inboundFaxSettings.redistribute.restrictions.push(this.modalEditAutoForwardRestriction);
    }

    this.restrictItems = this.inboundFaxSettings.redistribute.restrictions.map((e) => this.createRestrictTableItem(e));
    this.onCloseAutoForwardRestrictionModal();
  };

  onCloseAutoForwardRestrictionModal = (): void => {
    this.autoForwardRestrictionModalInstance.close();
    this.showAutoForwardRestrictionModal = false;
  };

  isAutoForwardModalValid(): boolean {
    this.errors = [];
    if (this.modalEditAutoForwardDestination.to.trim()) {
      if (
        this.inboundFaxSettings.redistribute.redistributions.some(
          (a) => a.to == this.modalEditAutoForwardDestination.to && a.to != this.autoForwardDestinationKey
        )
      ) {
        this.errors.push(this.translate.instant('admin-inbound-fax-settings.auto-forward-destination-unique'));
        return false;
      } else {
        return true;
      }
    }

    if (this.modalEditAutoForwardDestination.redistributeToType == RedistributeToType.Fax)
      this.errors.push(this.translate.instant('admin-inbound-fax-settings.must-add-fax-number'));

    if (this.modalEditAutoForwardDestination.redistributeToType == RedistributeToType.ContactList)
      this.errors.push(this.translate.instant('admin-inbound-fax-settings.must-add-contact-list'));

    return false;
  }

  isAutoForwardRestrictionModalValid(): boolean {
    this.errors = [];

    this.validateAutoForwardRestrictionCallerId();
    this.validateAutoForwardRestrictionCSID();

    if (
      this.inboundFaxSettings.redistribute.restrictions.some(
        (a) =>
          a.by.toLowerCase() == this.modalEditAutoForwardRestriction.by.toLowerCase() &&
          a.by.toLowerCase() != this.autoForwardRestrictionKey.toLowerCase()
      )
    )
      this.errors.push(this.translate.instant('admin-inbound-fax-settings.auto-forward-restriction-unique'));

    return this.errors.length == 0;
  }

  validateAutoForwardRestrictionCallerId(): void {
    if (this.modalEditAutoForwardRestriction.restrictByType != RestrictByType.CallerId) return;

    if (!this.modalEditAutoForwardRestriction.by.trim())
      this.errors.push(this.translate.instant('admin-inbound-fax-settings.must-add-caller-id'));
  }

  validateAutoForwardRestrictionCSID(): void {
    if (this.modalEditAutoForwardRestriction.restrictByType != RestrictByType.CSID) return;

    if (!this.modalEditAutoForwardRestriction.by.trim())
      this.errors.push(this.translate.instant('admin-inbound-fax-settings.must-add-csid'));

    if (this.modalEditAutoForwardRestriction.by.length > 20)
      this.errors.push(this.translate.instant('admin-inbound-fax-settings.csid-length-exceeds-limit'));
  }

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
