import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MetaDataType,
} from '../../../../../../../../../../Bamba.Shared';
import {
  HTTPBodyPartTypes,
  HTTPHeader,
  IHTTPBodyPart,
  IHTTPHeader,
} from '../../../../../../blocks/InterFAX/Models/webintegration';

@Component({
  selector: 'app-http-bodypart',
  templateUrl: './http-bodypart.component.html',
  styleUrls: ['./http-bodypart.component.scss'],
})
export class HttpBodypartComponent implements OnInit {
  @Input() bodyPart: IHTTPBodyPart;
  @Input() main: boolean;
  @Input() substitutionVariables: FixedDataSource;

  httpBodyTypesField: FieldDefinition;
  httpBodyTextField: FieldDefinition;

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  onAddHeader = (): void => {
    this.bodyPart.headers.push(new HTTPHeader());
  };

  onDeleteHeader = (header: IHTTPHeader): void => {
    const index = this.bodyPart.headers.indexOf(header);
    this.bodyPart.headers.splice(index, 1);
  };

  get bodyPartType(): string {
    return HTTPBodyPartTypes[this.bodyPart.type];
  }

  set bodyPartType(value: string) {
    this.bodyPart.type = HTTPBodyPartTypes[value];
  }

  get showBodyTextField(): boolean {
    if (this.bodyPart.type == HTTPBodyPartTypes.TextASCII) return true;
    if (this.bodyPart.type == HTTPBodyPartTypes.TextUTF8) return true;
    if (this.bodyPart.type == HTTPBodyPartTypes.TextUTF16) return true;

    return false;
  }

  getNameFieldFor = (header: IHTTPHeader): FieldDefinition => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: this.translateService.instant('admin-inbound-fax-settings.http-header-name-placeholder'),
      substitutable: true,
      datasource: this.substitutionVariables,
    });
  };

  getValueFieldFor = (header: IHTTPHeader): FieldDefinition => {
    return new FieldDefinition({
      type: MetaDataType.Text,
      label: '',
      placeholder: this.translateService.instant('admin-inbound-fax-settings.http-header-value-placeholder'),
      substitutable: true,
      datasource: this.substitutionVariables,
    });
  };

  createFields = (): void => {
    this.httpBodyTypesField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-inbound-fax-settings.http-body-type'),
      placeholder: '',
      required: true,
      datasource: this.httpMethodsDataSource(),
    });

    this.httpBodyTextField = new FieldDefinition({
      type: MetaDataType.LargeText,
      label: this.translateService.instant('admin-inbound-fax-settings.http-body-text'),
      placeholder: '',
      substitutable: true,
      datasource: this.substitutionVariables,
    });
  };

  httpMethodsDataSource(): FixedDataSource {
    const options = new Array<IFieldOptions>();

    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.http-body-part-text-ascii'),
      value: 'TextASCII',
    });
    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.http-body-part-text-utf8'),
      value: 'TextUTF8',
    });
    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.http-body-part-text-utf16'),
      value: 'TextUTF16',
    });
    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.http-body-part-document-pdf'),
      value: 'DocumentPDF',
    });
    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.http-body-part-document-tif'),
      value: 'DocumentTIF',
    });

    if (this.main)
      options.push({
        name: this.translateService.instant('admin-inbound-fax-settings.http-body-part-multipart'),
        value: 'Multipart',
      });

    return new FixedDataSource(options);
  }
}
