import { EventEmitter, OnDestroy, Output } from '@angular/core';
import { Component, Input, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, MetaDataType, ISearchItems } from 'shared-code-lib';
import { ShareWith } from '../../../../../blocks/InterFAX/Models/inbound';

@Component({
  selector: 'app-share-modal',
  templateUrl: './share-modal.component.html',
  styleUrls: ['./share-modal.component.scss'],
})
export class ShareModalComponent implements OnInit {
  @Input() share: ShareWith;
  @Input() permissionDataSource: FixedDataSource;
  @Input() selectedUsers: Array<ShareWith>;
  @Input() searchItems: ISearchItems;
  @Output() selectedUsersChange = new EventEmitter<Array<ShareWith>>();

  fieldPermissions: FieldDefinition;
  fieldUser: FieldDefinition;

  get sharePermissions() {
    return `${this.share.permissions}`;
  }
  set sharePermissions(val: string) {
    this.share.permissions = Number(val);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.createFields();
  }

  createFields = () => {
    this.fieldPermissions = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-inbound-fax-settings.column-permissions'),
      required: true,
      datasource: this.permissionDataSource,
    });

    this.fieldUser = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-inbound-fax-settings.auto-forward-destination'),
      required: true,
    });
  };

  currentChanged = (items: Array<ShareWith>): void => {
    this.selectedUsers = items;
    this.selectedUsersChange.emit(this.selectedUsers);
  };
}
