import { Component, OnInit, Input } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FieldDefinition, FixedDataSource, IFieldOptions, MetaDataType } from '../../../../../../../../../Bamba.Shared';
import { HttpConfiguration, HTTPBodyPart, IHTTPBodyPart } from '../../../../../blocks/InterFAX/Models/webintegration';

@Component({
  selector: 'app-webpost-advanced-http',
  templateUrl: './webpost-advanced-http.component.html',
  styleUrls: ['./webpost-advanced-http.component.scss'],
})
export class WebpostAdvancedHttpComponent implements OnInit {
  @Input() configuration: HttpConfiguration;
  @Input() substitutionVariables: FixedDataSource;

  httpMethodField: FieldDefinition;
  timeoutField: FieldDefinition;

  get configurationTimeout() {
    return `${this.configuration.timeoutSeconds}`;
  }

  set configurationTimeout(val: string) {
    this.configuration.timeoutSeconds = Number(val);
  }

  constructor(private translateService: TranslateService) {
    this.createFields();
  }

  ngOnInit(): void {}

  onAddBodyPart = (): void => {
    this.configuration.bodyParts.push(new HTTPBodyPart());
  };

  onDeleteBodyPart = (bodyPart: IHTTPBodyPart): void => {
    const index = this.configuration.bodyParts.indexOf(bodyPart);
    this.configuration.bodyParts.splice(index, 1);
  };

  createFields = (): void => {
    this.httpMethodField = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-inbound-fax-settings.http-advanced-method'),
      placeholder: '',
      required: true,
      datasource: this.httpMethodsDataSource(),
    });

    this.timeoutField = new FieldDefinition({
      type: MetaDataType.Integer,
      label: this.translateService.instant('admin-inbound-fax-settings.http-advanced-timeout'),
      placeholder: '',
      required: true,
      allowNegativeNumbers: false,
    });
  };

  httpMethodsDataSource(): FixedDataSource {
    const options = new Array<IFieldOptions>();

    options.push({ name: this.translateService.instant('admin-inbound-fax-settings.http-method-get'), value: 'Get' });
    options.push({ name: this.translateService.instant('admin-inbound-fax-settings.http-method-post'), value: 'Post' });
    options.push({
      name: this.translateService.instant('admin-inbound-fax-settings.http-method-delete'),
      value: 'Delete',
    });
    options.push({ name: this.translateService.instant('admin-inbound-fax-settings.http-method-put'), value: 'Put' });

    // TBD
    // options.push({ name: this.translateService.instant('admin-inbound-fax-settings.http-method-head'), value: 'Head' });
    // options.push({ name: this.translateService.instant('admin-inbound-fax-settings.http-method-options'), value: 'Options' });

    return new FixedDataSource(options);
  }
}
