import {
  Component,
  OnInit,
  ViewEncapsulation,
  Input,
  Output,
  EventEmitter,
  OnChanges,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { SortDescriptor } from '@progress/kendo-data-query';
import { TranslateService } from '@ngx-translate/core';
import {
  TableItem,
  ICombinedColumn,
  InvokeAction,
  UserPreferencesService,
  Pagination,
  DelayService,
} from 'shared-code-lib';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-fax-settings-table',
  templateUrl: './fax-settings-table.component.html',
  styleUrls: ['./fax-settings-table.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class FaxSettingsTableComponent implements OnInit, OnChanges {
  @ViewChild('table', { static: true }) table: any;

  @Input() items: TableItem[] = [];
  @Input() pageable: boolean;
  @Input() columns: ICombinedColumn[];
  @Input() sort: SortDescriptor;
  @Input() preferencesKey: string;
  @Input() numberOfDisplayedRows: number = 5;
  @Input() heightUnset: boolean = false;
  @Output() onAdd = new EventEmitter();
  @Output() onProperties = new EventEmitter<any>();
  @Output() onDelete = new EventEmitter<any>();

  filteredItems: TableItem[] = [];
  currentPage = 1;
  pageSize = 5;
  pageSizePreferenceKey: string = 'contactlist-contacts-pagesize';

  constructor(
    private translate: TranslateService,
    private ngxSmartModalService: NgxSmartModalService,
    private userPreferencesService: UserPreferencesService,
    private spinnerService: SpinnerStateService,
    private delay: DelayService
  ) {}

  ngOnInit() {
    this.translateColumnHeaderText();

    if (this.pageable) this.pageSize = this.userPreferencesService.getPreference(this.pageSizePreferenceKey, 25);

    this.displayCurrentPage();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.items) this.updateTableHeight();
  }

  updateTableHeight = (): void => {
    this.displayCurrentPage();
    const tableEl = this.table.grid.wrapper.nativeElement.parentElement.parentElement;
    if (!tableEl) return;

    if (this.heightUnset) return;

    if (
      !this.items ||
      !this.items.length ||
      !this.numberOfDisplayedRows ||
      this.items.length <= this.numberOfDisplayedRows
    ) {
      tableEl.style.height = 'initial';
      return;
    }

    tableEl.style.height = `${(this.numberOfDisplayedRows + 1) * 38}px`;
  };

  displayCurrentPage = (): void => {
    if (!this.pageable) {
      this.filteredItems = this.items;
      return;
    }

    const start = (this.currentPage - 1) * this.pageSize;
    const end = Math.min(start + this.pageSize, this.items.length);
    this.filteredItems = this.items.slice(start, end);

    this.spinnerService.hide();
  };

  onLoadPage = (pagination: Pagination): void => {
    if (pagination.pageSize != this.pageSize)
      this.userPreferencesService.setPreference(this.pageSizePreferenceKey, pagination.pageSize);

    this.currentPage = pagination.pageNumber;
    this.pageSize = pagination.pageSize;

    this.spinnerService.show(this.translate.instant('Common.Loading'), document.documentElement);
    this.delay.execute(this.displayCurrentPage, 10);
  };

  deleteItem(action: InvokeAction) {
    this.onDelete.emit(action.item.data);
  }

  translateColumnHeaderText = (): void => {
    if (!this.columns) return;

    for (const c of this.columns) {
      if (!c.headerText) continue;

      c.headerText = this.translate.instant(c.headerText);
    }
  };

  closeConfirmModal = () => this.ngxSmartModalService.getModal('actionConfirmModal').close();

  confirmedDelete = (rowIndex: number) => {};

  showAddModal() {
    this.onAdd.emit();
  }

  showUpdateModal(action: InvokeAction) {
    this.onProperties.emit(action.item);
  }

  confirmedDeleteItem = (rowIndex: number) => {};
}
