import { Component, Input, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import {
  FieldDefinition,
  IFieldDataSource,
  IFieldOptions,
  MetaDataType,
} from '../../../../../../../../../Bamba.Shared';
import { Inbound } from '../../../../../blocks/InterFAX/Models/inbound';
import { Workflow } from '../../../../../blocks/models/workflow';
import { HydraApiService } from '../../../../../blocks/services/hydra-api.service';

@Component({
  selector: 'app-webpost-uicworkflow',
  templateUrl: './webpost-uicworkflow.component.html',
  styleUrls: ['./webpost-uicworkflow.component.scss'],
})
export class WebpostUicworkflowComponent implements OnInit {
  @Input() inbound: Inbound;

  workflowsField: FieldDefinition;
  workflowDataSource: UserAccessibleWorkflowsDataSource;

  constructor(private hydraApi: HydraApiService) {}

  ngOnInit(): void {
    this.workflowDataSource = new UserAccessibleWorkflowsDataSource(this.hydraApi);

    this.createFields();
  }

  createFields = (): void => {
    this.workflowsField = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: false,
      datasource: this.workflowDataSource,
    });
  };
}

export class UserAccessibleWorkflowsDataSource implements IFieldDataSource {
  changed = new Subject<any>();
  callback: (items: IFieldOptions[]) => void;

  constructor(public hydraApi: HydraApiService) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    this.callback = successCallBack;
    this.hydraApi.getWorkflowsUserCanSubmitTo(this.workflowsLoaded, this.loadWorkflowsFailed);
  }

  loadWorkflowsFailed = (err: any) => {
    this.workflowsLoaded([]);
  };

  workflowsLoaded = (workflows: Array<Workflow>) => {
    const options = [];

    workflows.forEach((c) => {
      options.push({
        name: c.name,
        value: c.id,
      });
    });

    this.callback(options);
  };
}
