import { Component, Input } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { EnumDataSource, FieldDefinition, MetaDataType } from 'shared-code-lib';
import { EmailFormat, Notification } from '../../../../../blocks/InterFAX/Models/inbound';

@Component({
  selector: 'app-notification-email-modal',
  templateUrl: './notification-email-modal.component.html',
  styleUrls: ['./notification-email-modal.component.scss'],
})
export class NotificationEmailModalComponent {
  @Input() notification: Notification;
  fieldEmail: FieldDefinition;
  fieldEmailFormat: FieldDefinition;

  get notificationFormat() {
    return `${this.notification.format}`;
  }
  set notificationFormat(val: string) {
    this.notification.format = Number(val);
  }

  constructor(private translate: TranslateService) {
    this.createFields();
  }

  createFields = (): void => {
    this.fieldEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translate.instant('admin-inbound-fax-settings.notification-email'),
      placeholder: this.translate.instant('admin-inbound-fax-settings.notification-email-placeholder'),
      required: true,
    });

    const emailFormatDataSource = new EnumDataSource(EmailFormat, 'EmailFormatEnum', this.translate);
    emailFormatDataSource.options.sort((a, b) => a.name.localeCompare(b.name));

    this.fieldEmailFormat = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translate.instant('admin-inbound-fax-settings.notification-email-format'),
      required: false,
      datasource: emailFormatDataSource,
    });
  };

  getDigitalIdStatusText = (): string => {
    if (this.notification) {
      return this.notification.hasDigitalId
        ? this.translate.instant('admin-inbound-fax-settings.digital-id-uploaded')
        : this.translate.instant('admin-inbound-fax-settings.digital-id-not-uploaded');
    }

    return '';
  };
}
