import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { UserInboundBarcodes, BarcodeMapping, Inbound, PageLocation } from '../../../../blocks/InterFAX/Models/inbound';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar-portable';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { ToastrService } from 'ngx-toastr';
import {
  DelayService,
  EnumDataSource,
  FieldDefinition,
  FixedDataSource,
  IFieldOptions,
  MappingHandleMultiple,
  MetaDataType,
} from 'shared-code-lib';
import { InterFAXService } from '../../../../blocks/services/interfax.service';
import { CustomProperty } from '../../../../blocks/InterFAX/Models/admin';
import { Log } from '../../../../blocks/core/log';
import { SpinnerStateService } from '../../../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-inbound-barcodes-settings',
  templateUrl: './inbound-barcodes-settings.component.html',
  styleUrls: ['./inbound-barcodes-settings.component.scss'],
})
export class InboundBarcodesSettingsComponent implements OnInit {
  @ViewChild(PerfectScrollbarDirective, { static: true })
  directiveRef?: PerfectScrollbarDirective;

  @Input() inboundBarcodes: UserInboundBarcodes;

  onPageDataSource: FixedDataSource;
  storeInDataSource: FixedDataSource;
  handleMultipleDataSource: FixedDataSource;
  customPropertiesLoaded: boolean;

  constructor(
    private translate: TranslateService,
    private delay: DelayService,
    private interfaxService: InterFAXService,
    private toast: ToastrService,
    private spinner: SpinnerStateService
  ) {}

  ngOnInit(): void {
    this.onPageDataSource = new EnumDataSource(PageLocation, 'PageLocationEnum', this.translate);

    this.handleMultipleDataSource = new EnumDataSource(
      MappingHandleMultiple,
      'MappingHandleMultipleEnum',
      this.translate
    );

    this.spinner.show(this.translate.instant('Common.Loading'), document.documentElement);
    this.interfaxService.getCustomProperties(this.propertiesLoaded, this.propertiesLoadFailed);
  }

  createField = (
    label: string,
    placeholder: string,
    type: MetaDataType,
    allowNegativeNumbers = true,
    datasource: FixedDataSource = null
  ) => {
    return new FieldDefinition({
      type: type,
      label: label ? this.translate.instant(label) : '',
      placeholder: placeholder ? this.translate.instant(placeholder) : '',
      required: true,
      datasource: datasource,
      allowNegativeNumbers: allowNegativeNumbers,
    });
  };

  propertiesLoaded = (properties: Array<CustomProperty>): void => {
    this.spinner.hide();

    const options = new Array<IFieldOptions>();
    options.push({
      name: this.translate.instant(`admin-inbound-fax-settings.private-tag`),
      value: '#',
    });
    properties.sort((x, y) => x.name.toLowerCase().localeCompare(y.name.toLowerCase()));
    properties.forEach((p) => options.push({ name: p.name, value: `#${p.name}#${p.type}` }));

    this.storeInDataSource = new FixedDataSource(options);
    this.customPropertiesLoaded = true;
  };

  propertiesLoadFailed = (err): void => {
    this.spinner.hide();
    this.toast.error('admin-inbound-fax-settings.load-custom-properties-failed');
    Log.error('InboundBarcodesSettingsComponent.propertiesLoadFailed', err);
  };

  getPageValue = (mapping: BarcodeMapping): string => {
    if (Object.values(PageLocation).includes(mapping.page as any)) return mapping.page;

    return PageLocation.Custom;
  };

  onPageChanged = (mapping: BarcodeMapping, page: string): void => {
    mapping.page = page == PageLocation.Custom ? '1' : page;
  };

  setPageNumber = (mapping: BarcodeMapping, page: string): void => {
    mapping.page = page;
  };

  isCustomPageNumber = (value: string): boolean => !Object.values(PageLocation).includes(value as any);

  onAddValue = () => {
    if (!this.inboundBarcodes.barcodeMappings) this.inboundBarcodes.barcodeMappings = [];
    this.inboundBarcodes.barcodeMappings.push(new BarcodeMapping());

    this.delay.execute(() => {
      this.directiveRef?.scrollToBottom(0, 300);
    }, 100);
  };

  deleteAllItems = (): void => {
    this.inboundBarcodes.barcodeMappings = [];

    this.delay.execute(() => {
      this.directiveRef?.scrollToTop(0, 300);
    }, 100);
  };

  deleteItem = (item) => {
    const rowIndexToDelete = this.inboundBarcodes.barcodeMappings.indexOf(item);

    if (rowIndexToDelete > -1) {
      this.inboundBarcodes.barcodeMappings.splice(rowIndexToDelete, 1);
    }
  };

  dropped(event: CdkDragDrop<string[]>) {
    moveItemInArray(this.inboundBarcodes.barcodeMappings, event.previousIndex, event.currentIndex);
  }

  getItemMappingHandleMultiple = (i: number) => {
    return `${this.inboundBarcodes.barcodeMappings[i].mappingHandleMultiple}`;
  };
  setItemMappingHandleMultiple = (val: string, i: number) => {
    this.inboundBarcodes.barcodeMappings[i].mappingHandleMultiple = Number(val);
  };
}
